import { useState } from "react";

export default function GapStudio() {
  const [tab, setTab] = useState("create");
  const [prompt, setPrompt] = useState("");
  const [type, setType] = useState("image");
  const [result, setResult] = useState(null);
  const [loading, setLoading] = useState(false);

  async function handleGenerate(e) {
    e.preventDefault();
    setLoading(true);
    setResult(null);
    // Appel √† ton backend Node.js/Express
    const res = await fetch("https://ton-backend.com/api/gapstudio/generate", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ prompt, type }),
    });
    const data = await res.json();
    setResult(data);
    setLoading(false);
  }

  async function handleSave() {
    const user = "Visiteur"; // Remplace par l'utilisateur connect√© si besoin
    let content = type === "image" ? result.imageUrl : type === "musique" ? result.audioUrl : result.text;
    await fetch("https://ton-backend.com/api/gapstudio/save", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ user, type, prompt, content }),
    });
    alert("Cr√©ation sauvegard√©e !");
  }

  return (
    <div className="max-w-2xl mx-auto p-6 bg-white bg-opacity-10 rounded-xl mt-8">
      <h2 className="text-2xl font-bold mb-4 text-yellow-400">üé® GAP Studio IA</h2>
      <div className="flex mb-6">
        <button className={`flex-1 py-2 rounded-t-lg font-bold ${tab === "create" ? "bg-yellow-400 text-purple-900" : "bg-white text-purple-700 bg-opacity-80"}`} onClick={() => setTab("create")}>Cr√©er</button>
        <button className={`flex-1 py-2 rounded-t-lg font-bold ${tab === "explore" ? "bg-yellow-400 text-purple-900" : "bg-white text-purple-700 bg-opacity-80"}`} onClick={() => setTab("explore")}>Explorer</button>
        <button className={`flex-1 py-2 rounded-t-lg font-bold ${tab === "learn" ? "bg-yellow-400 text-purple-900" : "bg-white text-purple-700 bg-opacity-80"}`} onClick={() => setTab("learn")}>Apprendre</button>
      </div>

      {tab === "create" && (
        <form onSubmit={handleGenerate} className="mb-6">
          <input
            className="w-full p-2 mb-3 rounded"
            placeholder="D√©cris ton id√©e (ex: Un masque africain futuriste en violet et or)"
            value={prompt}
            onChange={e => setPrompt(e.target.value)}
            required
          />
          <select className="w-full p-2 mb-3 rounded" value={type} onChange={e => setType(e.target.value)}>
            <option value="image">Image</option>
            <option value="musique">Musique</option>
            <option value="texte">L√©gende/Po√®me</option>
          </select>
          <button type="submit" className="w-full py-2 bg-yellow-400 text-purple-900 font-bold rounded">G√©n√©rer</button>
        </form>
      )}

      {loading && <p className="text-center text-yellow-300">G√©n√©ration en cours...</p>}

      {result && tab === "create" && (
        <div className="my-4 text-center">
          {type === "image" && <img src={result.imageUrl} alt="Cr√©ation IA" className="mx-auto rounded-lg mb-2" style={{maxWidth: 400}} />}
          {type === "musique" && <audio controls src={result.audioUrl} className="mx-auto mb-2" />}
          {type === "texte" && <blockquote className="bg-yellow-100 bg-opacity-10 p-4 rounded text-yellow-300">{result.text}</blockquote>}
          <button onClick={handleSave} className="mt-3 py-2 px-4 bg-purple-700 text-yellow-200 rounded">Sauvegarder</button>
        </div>
      )}

      {tab === "explore" && (
        <div>
          <p className="mb-2">Pose une question culturelle √† GAP Studio (ex: "Quelle est la signification du masque Bamil√©k√© ?")</p>
          {/* √Ä compl√©ter : formulaire et affichage de la r√©ponse IA */}
        </div>
      )}

      {tab === "learn" && (
        <div>
          <ul className="text-left max-w-md mx-auto">
            <li>üñºÔ∏è Tutoriel : G√©n√©rer ton premier NFT avec l‚ÄôIA</li>
            <li>üé∂ Composer une musique inspir√©e d‚Äôune culture</li>
            <li>ü§ù Co-cr√©er une ≈ìuvre avec GAP Studio</li>
            <li>üé≤ Quiz et d√©fis cr√©atifs</li>
          </ul>
          <p className="text-yellow-400 mt-4">(Fonctionnalit√©s √† venir‚ÄØ: masterclass, ateliers live, etc.)</p>
        </div>
      )}
    </div>
  );
}